import React, { FC } from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import MuiBox from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import styled, { primaryTheme as theme } from '@theme';
import { ThemeProvider } from 'emotion-theming';
import { MuiThemeProvider } from '@material-ui/core/styles';
import { default as Header } from './header';
import { default as Footer } from './Footer';
import { default as ScrollToTop } from './ScrollToTop';
import { default as SEO } from './SEO';
import { default as GlobalStyle } from './GlobalStyle';

const Box = styled(MuiBox)`
    min-height: 100vh;
    margin-top: ${(props) => props.theme.spacing(10)}px;
    margin-bottom: ${(props) => props.theme.spacing(10)}px;
    ${(props) => props.theme.breakpoints.up('sm')} {
        margin-top: ${(props) => props.theme.spacing(20)}px;
        margin-bottom: ${(props) => props.theme.spacing(20)}px;
    }
`;

type IProps = {
    children: React.ReactNode;
    window?: () => Window;
    title?: string;
    ogImage?: string;
    isTransparent?: boolean;
};

const Layout: FC<IProps> = (props) => {
    const { children, title, isTransparent, ogImage } = props;
    return (
        <MuiThemeProvider theme={theme}>
            <ThemeProvider theme={theme}>
                <SEO title={title} ogImage={ogImage} />
                <CssBaseline />
                <GlobalStyle />
                <Header isTransparent={isTransparent} />
                <Container>
                    <Box>{children}</Box>
                </Container>
                <Footer />
                <ScrollToTop />
            </ThemeProvider>
        </MuiThemeProvider>
    );
};

export default Layout;
